import React, { useEffect, useState } from "react";

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => {
    images[item.replace("./", "")] = r(item);
  });
  return images;
}

const images = importAll(
  require.context("../assets", false, /\.(png|jpe?g|svg)$/)
);

